@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);
@import url(https://fonts.googleapis.com/css?family=Poiret+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #05386b;
  font-family: "Lato", sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search{
  border: 2px solid grey;
  width: 33%;
  border-radius: 10px;
  margin: auto ;
  margin-top: 5%;
  background-color: #ffffff;  
}
.searchTerm{
  border: none;
  width: 75%;
  padding: 6px 6px;
  border-radius: 10px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  height: 6vh;
}
.SearchBtn{
  background-color:#8ee4af;
  width: 25%;
  border-radius: 10px;
  padding: 5px 5px;
  height: 6vh;
  text-align: center;
  font-size: 1rem;
  color: white;
}
.SearchBtn:hover{
  cursor: pointer;
  background-color: #73db9b;
  color: aliceblue;
}
.widget{
  width: 50vw;
  height: 70vh;
  margin: auto;
  margin-top: 2%;
  background: #ffffff;
  border-radius: 25px;
  box-shadow: -6px -6px 10px #a3b1c6, 6px 6px 12px #a3b1c6;
  /* padding: 10px 10px; */
}
.description{
  /* border: 2px solid red; */
  display: grid;
  place-items: center;
  margin-right: 12%;
}
.weatherIcon{
  width: 100%;
  height: 50%;
  display: grid;
  place-items: center;
  font-size: 3rem;
  color:#3500d3 ;
}
.weatherIcon i{
  margin-top: 3%;
}
.weatherInfo{
  width: 100%;
  height: 27%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  background-color: #3500d3;
  color: #ffffff;
  font-size: 2rem;
}
.date{
  background-color: #73db9b;
  height: 100%;
  width: 100.2%;
  display: grid; 
  color: #ffffff;
  place-items: center; 
  font-size: 1rem;
  /* margin-left: 4%; */
  text-align: center;
}
.weatherInfo1{
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.weatherCondition{
  margin-top: 12%;
}
.temperature {
  display: flex;
  position: relative;
  font-size: 2rem;
  justify-content: space-around;  
  align-items: center;
}
.place{
  font-size: 1rem;
}
.temp-info-minmax{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top:5% ;
  padding-bottom: 1rem;
}
.two-sided-section{
  display: flex;
  justify-content: center;
  align-items: center;
}
.two-sided-section p{
  margin: 4%; 
  text-align: center;
}
.two-sided-section i{
  color: #3500d3;
   font-size: 2rem;
}

/* lets make responsive */
@media only screen and (max-width:850px){
  .widget{
    height: 75vh;
    width: 70vw;
  }
  .searchTerm{
    width: 60%;
  }
  .SearchBtn{
    width: 40%;
  }
  .weatherInfo{
    grid-template-columns: 1fr;
  }
}
@media only screen and (max-width:590px){
  .widget{
    height: 98vh;
    width: 80vw;
  }
  .search{
    width: 50%;
  }
  .temp-info-minmax{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (orientation:landscape) {
  .SearchBtn{
    height: 8vh;
  }
  .weatherCondition {
    margin-top: 2%;
}
.temp-info-minmax{
  margin-top: 2%;
}
}
@media (orientation:landscape)  and (max-height:300px){
.widget{
  height: 300vh;
}
.searchTerm{
  height: 19px;
}
.SearchBtn{
  height: 20px;
  padding: 0px;
}
.date{
  margin: 0px;
}
.weatherInfo{
  margin: 0px;
}
.weatherCondition{
  font-size: 20px;
}
.place{
  font-size: 10px;
}
.two-sided-section{
  font-size: 10px;
}
}
@media (orientation : landscape) and (max-width:850px) {
  .date{
    padding-top: 1%;
    padding-bottom: 1%;
    width: 100%;
  }
  .widget{
    width: 90vw;
    height: 100vh;
  }
  .temp-info-minmax{
    padding-top: 1%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}